/*公共样式*/
* {
  margin: 0;
  padding: 0;
}
html,
body {
  height: 100%;
}
html {
  background: url(../../static/img/bk.e6cb421.jpg);
}
.clearfix:after {
  content: "";
  visibility: hidden;
  display: block;
  clear: both;
  height: 0;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
a,
input,
select,
button,
span,
label,
div,
textarea {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -moz-tap-highlight-color: rgba(0, 0, 0, 0);
  -mz-tap-highlight-color: rgba(0, 0, 0, 0);
  -o-tap-highlight-color: rgba(0, 0, 0, 0);
}
input,
select,
button,
span,
label {
  outline: none;
  border: none;
}
textarea {
  resize: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  text-align: center;
  height: 100%;
  width: 10rem;
  margin: 0 auto;
  color: #2c3e50;
}
