.top {
  line-height: 1rem;
  width: 9rem;
  border-radius: 0px 0px 10px 10px;
  font-size: 18px;
  background: url(data:image/png;base64,) repeat-x;
  color: #fff;
  position: fixed;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
[data-dpr="2"] .top {
  font-size: 36px;
}
[data-dpr="3"] .top {
  font-size: 54px;
}
.top .nav .nav_li {
  width: 3rem;
  line-height: 0.8rem;
  display: inline-block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff;
}
.top .nav .nav_li:nth-child(1) {
  border-left: none;
}
.top .nav .nav_li:nth-last-child(1) {
  border-right: none;
}
.top .nav .open_code {
  color: #21b7e9;
}
